.App {
  text-align: center;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.App-header {
  background-color: #1f1f1f;
  min-height: 100vh;
  display: flex;
  font-size: calc(10px + 2vmin);
  color: white;
  position: relative;
}
.App-link {
  color: #61dafb;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.mainPage {
  font-size: calc(10px + 2vmin);
  color: #ffffff;
  height: calc(100vh - 5rem);
}
.gridPage {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(5, minmax(0, 1fr));
}
.menu_item {
  padding: 10px;
  color: var(--primary);
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 600;
  display: flex;
}
.classicText {
  font-size: var(--text-xs);
  line-height: var(--tw-leading, var(--text-xs--line-height));
  color: var(--color-gray-400);
  text-transform: uppercase;
}
.btnWIthBorders {
  height: 100%;
  --tw-tracking: var(--tracking-wider);
  letter-spacing: var(--tracking-wider);
  border-left: 1px solid rgba(255, 255, 255, 0.1);
  border-right: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 300ms linear;
}
.btnWIthBorders:hover {
  color: var(--primary);
}
.gridVisibileItem {
  width: 1px;
  height: auto;
  background-color: #6565651b;
  grid-row-start: 1;
  grid-row-end: 6;
}
.geologicalData:before {
  content: '';
  width: 15px;
  height: 2px;
  left: 2rem;
  top: 2.6rem;
  background-color: var(--primary);
  position: absolute;
  transition: all 700ms linear;
}
.geologicalData :first-child {
  color: transparent;
  transition: all .3s ease-in-out;
  transform: translateY(-2rem);
}
.geologicalData:hover :first-child {
  color: #9CA3AF;
  transform: translateY(10px);
}
.geologicalData :last-child {
  color: #9CA3AF;
  transition: all .3s ease-in-out;
  transform: translateY(-5px);
}
.geologicalData:hover :last-child {
  color: transparent;
  transform: translateY(2rem);
}
.tittle:after {
  content: '';
  width: 4rem;
  height: 2px;
  left: 0;
  bottom: -20px;
  margin-right: 10px;
  background-color: var(--primary);
  position: absolute;
  right: 10px;
  transition: all 700ms linear;
}
.beforeVertical:before {
  content: '';
  width: 4px;
  height: 10rem;
  left: 0;
  top: -20px;
  background-color: var(--primary);
  position: absolute;
  transition: all 700ms linear;
}
.buttonMovingRight:hover {
  transform: translateX(2.5rem);
  transition: all .3s ease-in-out;
}
.buttonMovingRight {
  transition: all .3s ease-in-out;
  transform: translateX(0);
}
.new-line {
  white-space: pre-line;
}
.chart {
  width: 150px;
  height: 150px;
  border-radius: 100%;
  display: grid;
  place-items: center;
  margin-bottom: 20px;
}
.chart::after {
  content: "";
  display: block;
  background: white;
  width: 100px;
  height: 100px;
  border-radius: 100%;
}
.menuBarItem {
  float: left;
  margin-right: calc(var(--spacing) * 10);
  margin-left: calc(var(--spacing) * 4);
  display: flex;
  align-items: center;
  --tw-font-weight: var(--font-weight-semibold);
  font-weight: var(--font-weight-semibold);
  --tw-tracking: var(--tracking-wide);
  letter-spacing: var(--tracking-wide);
  transition: all 700ms linear;
}
.menuBarItem:hover {
  color: #c3428f;
}
.menuBarItem:hover:before {
  background-color: var(--primary);
}
.menuBarItem:before {
  content: '';
  width: 15px;
  height: 2px;
  background-color: #6a7282;
  position: relative;
  right: 10px;
  transition: all 700ms linear;
}
